import * as React from 'react';
import PropTypes from 'prop-types';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';


import PhoneIcon from '@mui/icons-material/Phone';


function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`vertical-tabpanel-${index}`}
      aria-labelledby={`vertical-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.number.isRequired,
  value: PropTypes.number.isRequired,
};

function a11yProps(index) {
  return {
    id: `vertical-tab-${index}`,
    'aria-controls': `vertical-tabpanel-${index}`,
  };
}

export default function VerticalTabs() {
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };



  return ( <>
    <Box
      sx={{ flexGrow: 1, bgcolor: 'background.paper', display: 'flex', height: 224 , margin:10}}
    >
      <Tabs
        orientation="vertical"
        variant="scrollable"
        value={value}
        onChange={handleChange}
        aria-label="Vertical tabs example"
        sx={{ borderRight: 1, borderColor: 'divider' }}
      >
        <Tab label="LINKED BANK ACCOUNT" {...a11yProps(0)} />
        <Tab label="SUSPENDED  ACCOUNT" {...a11yProps(1)} />
        <Tab label="SELF VERIFIED ACCOUNT" {...a11yProps(2)} />
   
      </Tabs>
      <TabPanel value={value} index={0}>
        Item One
      </TabPanel>
      <TabPanel value={value} index={1}>
        Item Two
      </TabPanel>
      <TabPanel value={value} index={2}>
        Item Three
      </TabPanel>
    
    </Box>

    <div>
          <Tabs value={value} onChange={handleChange} aria-label="icon label tabs example"
                sx={{ flexGrow: 1, bgcolor: 'background.paper' , margin:10}}>
      <Tab icon={<PhoneIcon />} label="LINKED BANK ACCOUNT" />
      <Tab icon={<PhoneIcon />} label="SUSPENDED ACCOUNT" />
      <Tab icon={<PhoneIcon />} label="SELF VERIFIED ACCOUNT" />
    </Tabs>
    </div>
    </>
  );
}